@font-face {
  font-family: 'Cafe24Meongi-W-v1.0';
  src: url('https://fastly.jsdelivr.net/gh/projectnoonnu/2405-3@1.1/Cafe24Meongi-W-v1.0.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

.title {
  text-align: center;
  font-family: 'Cafe24Meongi-W-v1.0';
  font-size: 37px;
}

.list_wrap {
  padding: 20px 40px;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.item {
  display: grid;
  grid-template-rows: 327px minmax(auto, 40px) minmax(auto, 60px) auto;
  width: 280px;
  height: 620px;
  padding: 30px 30px;
  border: 1px solid #000;
  box-sizing: border-box;

  a {
    margin-top: 10px;
  }
  
  img {
    width: 100%;
    height: 327px;
  }

  p {
    margin-block-start: 0;
    margin-block-end: 0;
    height: 100%;               /* 높이를 고정 */
    display: -webkit-box;       /* 플렉스 박스 디스플레이 모드 */
    -webkit-box-orient: vertical; /* 수직 정렬 */
    overflow: hidden;           /* 넘치는 텍스트를 숨김 */
    text-overflow: ellipsis;    /* 넘치는 텍스트에 '...' 표시 */
  }
}